.note-view {
    background-color: #f3f3f3;
    height: 100vh;
}

.note-view p {
    line-height: 1.8;
}

.note-actions {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    font-size: 15px;
}

.note-actions a {
    text-decoration: underline;
}

.note-actions a:hover {
    color: #2BC1C4;
}

.note-delete {
    display: inline-block;
    width: 0;
}


